import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ActivatedRoute, Params, ParamMap } from '@angular/router';
import { DropdownModule } from 'primeng/dropdown';
import { BatchOperationService } from "app/pages/tzb/batch/http/batch-operation/batch-operation.http.service";
import { Message, SelectItem } from 'primeng/primeng';
import { QueryBatchJobConfigBean } from 'app/pages/tzb/batch/bean/batch-operation.bean';
import { prioritySwitch } from 'app/pages/tzb/batch/http/batch-operation/batch-operation.http.constant';
@Component({
    selector: 'query-work',
    templateUrl: './query-work.component.html',
    styleUrls: ['./query-work.component.scss'],
    providers: [BatchOperationService]
})
export class querywork implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    first: number = 0;
    tableData: any;// 表体内容
    display: boolean = false;
    total: string;//总页数
    headerTitle: string;
    tabMesg:any;
    msgs: Message[] = []; //提示信息
    prioritySwitchOptions: any = prioritySwitch;//优先级
    queryBatchJobConfigBean: QueryBatchJobConfigBean = new QueryBatchJobConfigBean();
    constructor(
        public httpBatchService: BatchOperationService
    ) {

    }

    ngOnInit() {
        this.queryBatchJobConfigBean.pageSize = '10';
        this.queryBatchJobConfigBean.pageNum = '1';
    }
    //分页
    paginate(event) {
        this.queryBatchJobConfigBean.pageSize = event.rows;
        this.queryBatchJobConfigBean.pageNum = event.page + 1;
        this.first = event.page * event.rows;
        this.queryClick();
    }
    queryFirst() {
        this.queryBatchJobConfigBean.pageSize = '10';
        this.queryBatchJobConfigBean.pageNum = '1';
        this.first = 0;
        this.queryClick();
    }
    choose(data) {
        this.outValue.emit(data);
    }
    queryClick() {
        this.httpBatchService.queryBatchJobConfig(this.queryBatchJobConfigBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.jobModelList
                this.total = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用失败！' });
        })
    }
}
