import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ActivatedRoute, Params, ParamMap } from '@angular/router';
import { DropdownModule } from 'primeng/dropdown';
import { BatchTaskConfigSellingBean } from "app/pages/tzb/batch/bean/batch-operation.bean";
import { BatchOperationService } from "app/pages/tzb/batch/http/batch-operation/batch-operation.http.service";
import { Message, SelectItem } from 'primeng/primeng';
import { prioritySwitch } from 'app/pages/tzb/batch/http/batch-operation/batch-operation.http.constant';
import { Validators, FormControl, AbstractControl, FormGroup, FormBuilder } from '@angular/forms';
import { createBactJobStepConfigBean } from 'app/pages/tzb/batch/bean/batch-operation.bean';
@Component({
    selector: 'deploy-add',
    templateUrl: './deploy-add.component.html',
    styleUrls: ['./deploy-add.component.scss'],
    providers: [BatchOperationService]
})
export class DeployAdd implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    // showComponent:any;
    tableData: any;// 表体内容
    total: string;//总页数
    showEmpty: boolean = true;
    showEmpty1: boolean = true;
    showEmpty2: boolean = true;
    msgs: Message[] = []; //提示信息
    public distributeCustQryForm: FormGroup;//校验
    prioritySwitch: any = prioritySwitch;//优先级
    crossSellingBean: BatchTaskConfigSellingBean = new BatchTaskConfigSellingBean()
    createBactJobStepConfigBean: createBactJobStepConfigBean = new createBactJobStepConfigBean();
    constructor(public batchOperationService: BatchOperationService, public fb: FormBuilder) {

    }

    ngOnInit() {
        this.crossSellingBean.pageSize = 10;
        this.crossSellingBean.pageNum = 1;



        this.distributeCustQryForm = this.fb.group({
            'jobModelNum': new FormControl('', Validators.required),  //作业标识
            'jobModelName': new FormControl('', Validators.required),//作业名称
            'prioritySwitch': new FormControl('', Validators.required),//作业名称
            
        });
    }


    //添加
    addClick1() {
        if (!this.crossSellingBean.jobModelId) {
            this.showEmpty1 = false;
        }
        if (!this.crossSellingBean.jobModelName) {
            this.showEmpty = false;
        }
        //校验的
        for (const i in this.distributeCustQryForm.controls) {
            this.distributeCustQryForm.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.distributeCustQryForm.controls) {
            if (!this.distributeCustQryForm.controls[i].valid) {
                return;
            }
        }
        this.batchOperationService.createBatchJobConfig(this.crossSellingBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.jobModelId;
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '保存成功！' });
                this.batchOperationService.queryBatchJobConfig(this.crossSellingBean)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '保存失败！' });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '保存失败！' });
        })

    }
    //重置
    reset() {
        this.crossSellingBean.jobModelId = '';
        this.crossSellingBean.jobModelName = '';
        this.crossSellingBean.jobModelNum = '';
    }


}
