import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { BatchComponent } from './batch.component';
import { BatchManage } from './batch-manage/batch-manage.component';
import { Controler } from './batch-manage/controler/controler.component';
//页面

const routes: Routes = [
    {
        path: '',
        component: BatchComponent,
        children: [
            { path: '', redirectTo: 'batch-manage', pathMatch: 'full' },
            {
                path: 'batch-manage',
                component: BatchManage,
                children: [
                    { path: '', redirectTo: 'controler', pathMatch: 'full' },
                    {
                        path: 'controler',
                        component: Controler
                    }
                ]
            },
        ]
    }
];
export const BatchRouting: ModuleWithProviders = RouterModule.forChild(routes);