import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ActivatedRoute, Params, ParamMap } from '@angular/router';
import { Router } from '@angular/router';
import { ConfirmationService } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { DomSanitizer } from '@angular/platform-browser';
import * as _ from 'lodash';
// import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Validators, FormControl, AbstractControl, FormGroup, FormBuilder } from '@angular/forms';
import { Message, SelectItem } from 'primeng/primeng';
import { BatchOperationService } from "app/pages/tzb/batch/http/batch-operation/batch-operation.http.service";
import { BatchTaskConfigSellingBean } from "app/pages/tzb/batch/bean/batch-operation.bean";

@Component({
    selector: 'batch',
    styleUrls: ['./batch.component.scss'],
    templateUrl: './batch.component.html',
    providers: [BatchOperationService]
})

export class BatchComponent implements OnInit {
    display1: boolean = false;
    display: boolean = false;
    headerTitle: string;
    showModel: string;
    prodNum: string;
    tabMesg:any;
    radio: boolean = true;
    public distributeCustQryForm: FormGroup;//校验
    // 表格中显示的信息
    // tabMesg: any = tableMessage;
    // 表体内容
    tableData: any;
    inValue: any;
    crossSellingBean: BatchTaskConfigSellingBean = new BatchTaskConfigSellingBean()
    //总页数
    total: string;
    //每页多少条
    pageSize: number = 10;

    //提示信息
    msgs: Message[] = [];
    productNum: any = [];



    constructor(
        public batchOperationService: BatchOperationService,
        public fb: FormBuilder,
    ) {

    }

    ngOnInit() {
        this.crossSellingBean.pageSize = 10;
        this.crossSellingBean.pageNum = 1;
        this.queryClick();
       
    }


    //查询
    queryClick() {
        this.batchOperationService.queryBatchJobConfig(this.crossSellingBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.jobModelList;
                this.total = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '查询失败！' });
        })
    }

    //分页
    paginate(event) {
        //每页显示的条数
        this.crossSellingBean.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1;
        this.crossSellingBean.pageNum = currentPage;
        //调用查询的方法
        this.queryClick();
        this.crossSellingBean.pageNum = 1;
    }

    // 重置
    reset() {
        this.crossSellingBean.jobModelId = '';
        this.crossSellingBean.jobModelName = '';
        this.crossSellingBean.jobModelNum = '';
    }
    addClick() {
        
        this.display1= true;
        this.headerTitle = '配置作业添加';


    }
    //添加回调
    allotCall(event) {

        this.display1 = false;
        this.queryClick()
    }
    // deleteClick() {

    // }
    //查看步骤
    seachStep(param) {
        this.display = true;
        this.headerTitle = '查看步骤'
        this.inValue=param;

    }
    deleteJobfig(data){
        let temp = {
            scheduleId: data.scheduleId
          }
          this.batchOperationService.deleteSchedule(temp).subscribe(data => {
            if (data.returnCode.code == 'success') {
              this.queryClick();
              this.msgs = [];
              this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功' });
            } else {
              this.msgs = [];
              this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
          }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用失败！' });
          })

}
}