import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ActivatedRoute, Params, ParamMap } from '@angular/router';
import { DropdownModule } from 'primeng/dropdown';
import { BatchTaskConfigSellingBean } from "app/pages/tzb/batch/bean/batch-operation.bean";
import { BatchOperationService } from "app/pages/tzb/batch/http/batch-operation/batch-operation.http.service";
import { Message, SelectItem } from 'primeng/primeng';
import { prioritySwitch } from 'app/pages/tzb/batch/http/batch-operation/batch-operation.http.constant';
@Component({
  selector: 'steptype',
  templateUrl: './steptype.component.html',
  styleUrls: ['./steptype.component.scss'],
  providers: [BatchOperationService]
})
export class steptype implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;
  // showComponent:any;
  tableData: any;// 表体内容
  display2: boolean = false;
  total: string;//总页数
  headerTitle: string;
  tabMesg:any;
  msgs: Message[] = []; //提示信息
  prioritySwitch: any = prioritySwitch;
  crossSellingBean: BatchTaskConfigSellingBean = new BatchTaskConfigSellingBean()
  constructor(public batchOperationService: BatchOperationService) {

  }

  ngOnInit() {
    this.crossSellingBean.pageSize = 10;
    this.crossSellingBean.pageNum = 1;
    //   let param={
    //     jobModelId:this.inValue.jobModelId
    //   }
    //   this.batchOperationService.queryBatchJobStepConfig(this.crossSellingBean).subscribe(data => {
    //     if (data.returnCode.code == 'success') {
    //         this.tableData = data.jobModelList;
    //         this.total = data.total;
    //     } else {
    //         this.msgs = [];
    //         this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
    //     }
    // }, error => {
    //     this.msgs = [];
    //     this.msgs.push({ severity: 'error', summary: '提示', detail: '查询失败！' });
    // })
  }
  ngOnChanges() {
    
    let param = {
      jobModelId: this.inValue.jobModelId//入参
    }
    this.batchOperationService.queryBatchJobStepConfig(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.stepModelList;//出
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '查询失败！' });
    })
  }

  queryClick() {
    this.batchOperationService.queryBatchJobConfig(this.crossSellingBean).subscribe(data => {
        if (data.returnCode.code == 'success') {
            this.tableData = data.jobModelList;
            this.total = data.total;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        }
    }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '查询失败！' });
    })
}

  addClick() {
    this.display2 = true;
    this.headerTitle = '作业步骤添加';
    
  }
  //添加回调
  allotCall(event) {

    this.display2 = false;
    this.queryClick()
}
}
