import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ActivatedRoute, Params, ParamMap } from '@angular/router';
import { DropdownModule } from 'primeng/dropdown';

import { BatchOperationService } from "app/pages/tzb/batch/http/batch-operation/batch-operation.http.service";
import { Message, SelectItem } from 'primeng/primeng';
import { prioritySwitch } from 'app/pages/tzb/batch/http/batch-operation/batch-operation.http.constant';
import { UpdateBatchScheduleBean } from 'app/pages/tzb/batch/bean/batch-operation.bean';
import { ScanType, excuteHz } from 'app/pages/tzb/batch/http/batch-operation/batch-operation.http.constant';
import {DatePipe} from '@angular/common';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Validators, FormControl, AbstractControl, FormGroup, FormBuilder } from '@angular/forms';
@Component({
    selector: 'timing-update',
    templateUrl: './timing-update.component.html',
    styleUrls: ['./timing-update.component.scss'],
    providers: [BatchOperationService,DatePipe]
})
export class timingupdate implements OnInit, OnChanges {
    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    @Output() outDisplay: EventEmitter<any> = new EventEmitter();
    excuteHzOptions: any = excuteHz;//执行频率
    scanTypeOptions: any = ScanType;//执行类型
    ch: any = CALENDAR_CN;
    tableData: any;// 表体内容
    qrydisplay: boolean = false;
    total: string;//总页数
    showEmpty: boolean = true;
  showEmpty1: boolean = true;
  showEmpty2: boolean = true;
  public distributeCustQryForm: FormGroup;//校验
    headerTitle: string;
    msgs: Message[] = []; //提示信息
    prioritySwitch: any = prioritySwitch;
    startTime:any;
    updateBatchScheduleBean: UpdateBatchScheduleBean = new UpdateBatchScheduleBean();
    constructor(
        public httpBatchService: BatchOperationService,
        public datePipe:DatePipe,
        public fb: FormBuilder
    ) {

    }

    ngOnInit() {
        this.distributeCustQryForm = this.fb.group({
            'jobModelNum': new FormControl('', Validators.required),  //作业标识
            'scheduleName': new FormControl('', Validators.required),//定时名称
            'scanType': new FormControl('', Validators.required),//执行类型
            'frequency': new FormControl('', Validators.required),  //执行频率
            'startTime': new FormControl('', Validators.required),//起始时间
      
        });
    }
    ngOnChanges() {
        this.startTime=new Date(this.inValue.startTime);
        this.updateBatchScheduleBean.interval=this.inValue.executeInterval;
        this.updateBatchScheduleBean = this.inValue;
    }
    querythiswork(event) {
        console.log(event);
        this.updateBatchScheduleBean.jobModelNum = event.jobModelNum;
        this.qrydisplay = false;
    }
    fondThis() {
        this.qrydisplay = true;
        this.headerTitle = '配置作业查询';
    }
    updateSave() {
        if (!this.updateBatchScheduleBean.jobModelNum) {
            this.showEmpty1 = false;
        }
        if (!this.updateBatchScheduleBean.scheduleName) {
            this.showEmpty = false;
        }
        //校验的
        for (const i in this.distributeCustQryForm.controls) {
            this.distributeCustQryForm.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.distributeCustQryForm.controls) {
            if (!this.distributeCustQryForm.controls[i].valid) {
                return;
            }
        }
        this.updateBatchScheduleBean.startTime=this.datePipe.transform(this.startTime,'yyyy/MM/dd HH:mm:ss');
        this.httpBatchService.updateBatchSchedule(this.updateBatchScheduleBean).subscribe(data => {
            if (data.returnCode.code == 'success') {

                this.outValue.emit(false);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用失败！' });
        })
    }
    cloose() {
        this.outDisplay.emit(false);
    }


}
