import { inject } from '@angular/core/testing';
import { Injectable, NgModule } from '@angular/core';
import { API } from './batch-operation.http.constant';
import { HttpService } from 'app/@uisftech/common/service/http.service';
import { TZB_HTTP_CUS, TZB_HTTP_CUS1, TZB_HTTP_PRO } from 'app/@uisftech/common/service/http.constant';
import { TZB_HTTP_CUS2 } from 'app/@uisftech/common/service/http.constant';
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { TZB_COM_TOKEN, TZB_HTTP_CUS3, TZB_HTTP_COM, TZB_HTTP_COM1, TZB_HTTP_CUS22, TZB_HTTP_CUS4, TZB_PRO_DOWNLOAD, TZB_HTTP_CUS222, TZB_HTTP_CUSz } from 'app/@uisftech/common/service/http.constant';
// import {TZB_HTTP_CUSGROUP} from 'app/@uisftech/common/service/http.constant'//客户群名单批量导入
/**
 * 客户运营平台
 * 模块下的接口服务
 */
@Injectable()
export class BatchOperationService {
    constructor(public httpService: HttpService) {
    }
    queryBatchJobConfig(params) {
        //params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_CUS + API.queryBatchJobConfig, params);
    }
    createBatchJobConfig(params) {
        // params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_CUS + API.createBatchJobConfig, params);
    }
    queryScheduleConfig(params) {
        //  params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_CUS + API.queryScheduleConfig, params);
    }
    queryBatchJobStepConfig(params) {
        //  params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_CUS + API.queryBatchJobStepConfig, params);
    }
    createBatchJobStepConfig(params) {
        return this.httpService.post(TZB_HTTP_CUS + API.createBatchJobStepConfig, params);
    }
    querySchedule(params) {
        return this.httpService.post(TZB_HTTP_CUS + API.querySchedule, params);
    }
    deleteBatchSchedule(params) {
        return this.httpService.post(TZB_HTTP_CUS + API.deleteBatchSchedule, params);
    }
    createBatchSchedule(params) {
        return this.httpService.post(TZB_HTTP_CUS + API.createBatchSchedule, params);
    }
    updateBatchSchedule(params) {
        return this.httpService.post(TZB_HTTP_CUS + API.updateBatchSchedule, params);
    }
    deleteSchedule(params) {
        return this.httpService.post(TZB_HTTP_CUS + API.deleteSchedule, params);
    }
}