import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ActivatedRoute, Params, ParamMap } from '@angular/router';
import { DropdownModule } from 'primeng/dropdown';
import * as _ from 'lodash';
import { prioritySwitch } from 'app/pages/tzb/batch/http/batch-operation/batch-operation.http.constant';
import { BatchOperationService } from "app/pages/tzb/batch/http/batch-operation/batch-operation.http.service";
import { Message, SelectItem } from 'primeng/primeng';
import { QueryScheduleBean } from 'app/pages/tzb/batch/bean/batch-operation.bean';
import { synchronous, excuteHz } from 'app/pages/tzb/batch/http/batch-operation/batch-operation.http.constant';
@Component({
  selector: 'check-step',
  templateUrl: './check-step.component.html',
  styleUrls: ['./check-step.component.scss'],
  providers: [BatchOperationService]
})

export class CheckStep implements OnInit {
  inValue: any;
  first: number = 0;
  synchronousOptions: any = synchronous;//定时类型
  excuteHzOptions:any=excuteHz;//定时频率
  showComponent: any;
  display: boolean = false;
  headerTitle: string;
  tableData: any;
  total: string;
  tabMesg:any;
  //提示信息
  msgs: Message[] = [];
  productNum: any = [];
  showNum: number;
  queryScheduleBean: QueryScheduleBean = new QueryScheduleBean();
  constructor(
    public httpBatchService: BatchOperationService
  ) { }

  ngOnInit() {
    this.queryScheduleBean.pageSize = '10';
    this.queryScheduleBean.pageNum = '1';
    this.first = 0;
    this.queryClick();

  }
  //分页
  paginate(event) {
    this.queryScheduleBean.pageSize = event.rows;
    this.queryScheduleBean.pageNum = event.page + 1;
    this.first = event.page * event.rows;
    this.queryClick();
  }
  queryFirst() {
    this.queryScheduleBean.pageSize = '10';
    this.queryScheduleBean.pageNum = '1';
    this.first = 0;
    this.queryClick();
  }
  queryClick() {
    this.httpBatchService.querySchedule(this.queryScheduleBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.scheduleConfigList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用失败！' });
    })
  }

  addClick() {
    this.display = true;
    this.showNum = 1;
    this.headerTitle = '定时配置添加';
  }
  //新增
  allotCall(event) {
    console.log(event)
    this.display=false;
    this.queryClick();
  }
  //新增回调  关闭模态框
  outThis(event) {
    this.display = event;
  }
  //修改
  update(data) {
    this.display = true;
    this.showNum = 2;
    this.headerTitle = '修改定时配置';
    this.inValue = data;
  }
  //删除
  delete(data) {
    let temp = {
      scheduleId: data.scheduleId
    }
    this.httpBatchService.deleteBatchSchedule(temp).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.queryClick();
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用失败！' });
    })
  }
  //修改
  showupdate(event) {
    console.log(event)
    this.display = false;
    this.queryClick();
  }
  //修改回调关闭
  displayUpdate(event) {
    this.display = event;
  }
}
