import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ActivatedRoute, Params, ParamMap } from '@angular/router';
import { DropdownModule } from 'primeng/dropdown';
import { BactJobStepConfigBean } from "app/pages/tzb/batch/bean/batch-operation.bean";
import { BatchOperationService } from "app/pages/tzb/batch/http/batch-operation/batch-operation.http.service";
import { Message, SelectItem } from 'primeng/primeng';
//import { prioritySwitch } from 'app/pages/tzb/batch/http/batch-operation/batch-operation.http.constant';
import {steptype}from'app/pages/tzb/batch/http/batch-operation/batch-operation.http.constant';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
@Component({
  selector: 'revamp1',
  templateUrl: './revamp1.component.html',
  styleUrls: ['./revamp1.component.scss'],
  providers: [BatchOperationService]
})
export class revamp1 implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  // showComponent:any;
  tableData: any;// 表体内容
  display: boolean = false;
  total: string;//总页数
  headerTitle: string;
  display1: number;
  public distributeCustQryForm: FormGroup;//校验
  msgs: Message[] = []; //提示信息
 // prioritySwitch: any = prioritySwitch;
  steptype: any = steptype;
  crossSellingBean: BactJobStepConfigBean = new BactJobStepConfigBean()
  constructor(public batchOperationService: BatchOperationService,public fb: FormBuilder,) {
    

  }

  ngOnInit() {
    this.distributeCustQryForm = this.fb.group({
      '': new FormControl(''),//

    });
  }
  //添加步骤
  submit() {
      this.batchOperationService.createBatchJobStepConfig(this.crossSellingBean).subscribe(data => {
        if (data.returnCode.code == 'success') {
          // this.tableData = data.jobModelId;
          this.outValue.emit(false);
          this.msgs = [];
          this.msgs.push({ severity: 'success', summary: '提示', detail: '保存成功！' });

        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '保存失败！' });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '保存失败！' });
      })

    }
  }

